.ChatApp{
    background-image: url(/VillePellosniemi/react_projekti/static/media/giphy.0b5934b6.gif);
    background-color: #f9f9f9;
    position: relative;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    overflow: hidden;
    box-shadow: 0 20px 50px rgb(23, 32, 90);
    border: 2px solid #2a3cad;
    color: white;
    padding: 20px;
}

.ChatApp:before{
    content: '';
    position:absolute;
    top:0;
    left:-100%;
    width:100%;
    height:100%;
    background: rgba(255,255,255,0.1);
    -webkit-transition:0.5s;
    transition:0.5s;
    pointer-events: none;
}

/*.ChatApp:hover:before{
    left:-50%;
    transform: skewX(-5deg);
}*/


.ChatApp .content{
    position:absolute;
    top:15px;
    left:15px;
    right:15px;
    bottom:15px;
    border:1px solid #f0a591;
    padding:20px;
    text-align:center;
    box-shadow: 0 5px 10px rgba(9,0,0,0.5);

}

.ChatApp span{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    box-sizing: inherit;
}

.ChatApp span:nth-child(1)
{
    -webkit-transform:rotate(0deg);
            transform:rotate(0deg);
}

.ChatApp span:nth-child(2)
{
    -webkit-transform:rotate(180deg);
            transform:rotate(180deg);
}


.ChatApp span:before
{
    content: '';
    position: absolute;
    width: 100vw;
    height: 2px;
    background: #50dfdb;
    -webkit-animation: animate 4s linear infinite;
            animation: animate 4s linear infinite;
}

@-webkit-keyframes animate {
    0% {
        -webkit-transform:scaleX(0);
                transform:scaleX(0);
        -webkit-transform-origin: left;
                transform-origin: left;
    }
    50%
    {
        -webkit-transform:scaleX(1);
                transform:scaleX(1);
        -webkit-transform-origin: left;
                transform-origin: left;
    }
    50.1%
    {
        -webkit-transform:scaleX(1);
                transform:scaleX(1);
        -webkit-transform-origin: right;
                transform-origin: right;

    }

    100%
    {
        -webkit-transform:scaleX(0);
                transform:scaleX(0);
        -webkit-transform-origin: right;
                transform-origin: right;

    }


}

@keyframes animate {
    0% {
        -webkit-transform:scaleX(0);
                transform:scaleX(0);
        -webkit-transform-origin: left;
                transform-origin: left;
    }
    50%
    {
        -webkit-transform:scaleX(1);
                transform:scaleX(1);
        -webkit-transform-origin: left;
                transform-origin: left;
    }
    50.1%
    {
        -webkit-transform:scaleX(1);
                transform:scaleX(1);
        -webkit-transform-origin: right;
                transform-origin: right;

    }

    100%
    {
        -webkit-transform:scaleX(0);
                transform:scaleX(0);
        -webkit-transform-origin: right;
                transform-origin: right;

    }


}

